<template>
  <div class="subject1">
    <div class="top" id="top" @click="topclick">
      <div class="subject">
        <span>题目 </span>
        <span>{{current}}</span>
        <span>/</span>
        <span>{{total}}</span>
      </div>
      <div class="time">
        <span>剩余时间 : </span><span>{{time}}</span>
      </div>

    </div>
    <div class="content">
      <div class="contentSubject">
        <div class="title">
          <span>{{title}}</span>

        </div>
        <div class="select">
          <div class="option">
            <label>
              <input type="radio" name="applicationSystem" value="1"  v-model="option">
              option1
            </label>
          </div>
          <div class="option">
            <label>
              <input type="radio" name="applicationSystem" value="2"  v-model="option">
              option2
            </label>
          </div>
          <div class="option">
            <label>
              <input type="radio" name="applicationSystem" value="3"  v-model="option">
              option3
            </label>
          </div>
          <div class="option">
            <label>
              <input type="radio" name="applicationSystem" value="4"  v-model="option">
              option4
            </label>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom">
      <div class="circular">
        <span class="point" :class="{'actived': isAnswer[0]}"></span>
        <span class="point" :class="{'actived': isAnswer[1]}"></span>
        <span class="point" :class="{'actived': isAnswer[2]}"></span>
        <span class="point" :class="{'actived': isAnswer[3]}"></span>
        <span class="point" :class="{'actived': isAnswer[4]}"></span>
      </div>
      <button @click="submit" v-if="isSubmit" class="submit" :style="{'right': right}"  :disabled="isDisabled" :class="{'active': isActive}">提交</button>
      <button class="endAnswer" v-if="isEndAnswer" :class="{'active': isActive1}">结束答题</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "subject1",
  data(){
    return {
      time: "5: 00",
      current: 2,
      total: 2,
      option: '',
      right: '30px',
      isEndAnswer: false,
      isSubmit: true,
      isDisabled: true,
      isActive: false,
      isActive1: false,
      count: 1,//纪录 是第几次提交
      index : 0,//纪录底部信号标识的当前位置
      signal: 0,//标识别人是否提交的信号
      members: 5,//组内人数
      title:'1. (单选） 党的十九大报告指出，提拔牢固树立“四个意识”和“四个自信”，坚决维护党中央权威，全面贯彻执行党的理论和路线方针政策，_______的好干部。',

    }
  },
  created() {
  },
  mounted() {
    this.setIsEndAnswer();
  },
  methods:{
    submit(){
      //第一次提交答案
      if(this.count === 1){
        this.$toast.show({
          message: '提交成功,你现在可以和组员讨论并重选答案',
          duration: 3000
        })
        this.option = '';
        this.isDisabled = true;
        this.isAnswer[this.index] = 'true';
        this.index++;
        this.count++;
      }
      //重新修改后提交答案
      else{
        this.$toast.show({
          isShow: true,
          message: '提交成功！',
          duration: 3000
        })
        this.isDisabled = true;
      }

    },
    topclick(){
      if(this.signal < this.members){
        this.signal++;
      }

    },
    setIsAnswer(){
      this.isAnswer.push('true');
      this.index++;
    },
    setIsEndAnswer(){
      this.isEndAnswer = true;
      this.right = '180px'
    }
  },
  computed:{
    isAnswer(){
      return [];
    }
  },
  watch:{
    option(){
      console.log("1111111");
      if(this.option.length !== 0){
        this.isDisabled = false;
        this.isActive = true
      }
      else{
        this.isDisabled = true;
        this.isActive = false
      }

    },
    //监听其他组员是否完成答题
    signal()
    {
      console.log('22222');
      this.isAnswer.push('true');
      this.$forceUpdate();
      if(this.signal === 4){
        this.isSubmit = false;
        this.isActive1 = true
      }
    }
  }
}
</script>

<style scoped>
.subject1{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.top{
  position: relative;
  width: 100%;
  height: 10%;
  border-bottom: 2px solid gray;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
  font-size: 20px;
}
.top .subject{
  margin-left: 50px;
}
.top .time{
  margin-right: 50px;
}
.content{
  width: 100%;
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.content .contentSubject{
  width: 100%;
  height: 100%;
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

}
.contentSubject .title{
  min-height: 50px;
  height: 30%;
  margin: 0 30px;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 19px;
  font-weight: 500;
  line-height: 30px;
  letter-spacing: 1px
}
.contentSubject .select{
  margin: 20px  100px 0 100px;
  min-height: 50px;
  height: 70%;
  text-align: left;
  font-size: 20px;
}
.select .option{
  margin-top: 10px;
}
.bottom{
  width: 100%;
  height: 10%;
  border-top: 2px solid gray;
  position: relative;
}
.bottom .circular{
  position: absolute;
  left: 50px;
}
.bottom .point{
  float: left;
  width: 20px;
  height: 20px;
  border-radius: 20px;
  background-color: white;
  border: 1px solid black;
  margin-right: 10px;
}
.bottom .actived{
  background-color: #7f7ffd;
}
.subject1 button {
  width: 120px;
  height: 44px;
  border-radius: 12px;
  font-family: PingFangSC-Medium;
  font-size: 18px;
  line-height: 18px;
  font-weight: 500;
  color: black;
  background-color: #f1efef;
  border: none;
}
.subject1 .next{
  background-color: #7f7ffd;
  color: white;
}
.subject1 .bottom  .submit, .next, .endAnswer{
  position: absolute;
  right: 30px;
}
.subject1 .bottom .active{
  background-color: #7f7ffd;
  color: white;
}
</style>