<template>
<div class="toast" v-if="isShow">
  <span>{{message}}</span>

</div>
</template>

<script>
export default {
  name: "Toast",
  data(){
    return {
      isShow: false,
      message: '',
      duration: 2000
    }
  },
  methods:{
    show(alias){
      this.isShow = alias.isShow || true;
      this.message = alias.message || '你好';
      this.duration = alias.duration || 2000;
      setTimeout(() => {
        this.isShow = false;
        this.message = '';
      }, this.duration)
    }
  }
}
</script>

<style scoped>
.toast{
  width: 600px;
  height: 50px;
  position: absolute;
  top: 57px;
  left: 376px;
  background-color: #eae9e9;
  text-align: center;
  font-size: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-space: 2px;

}
</style>