import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '../views/Home.vue'
import subject1 from "../views/subject1";
import subject2 from "../views/subject2";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/subject1',
    component: subject1
  },
  {
    path: '/subject2',
    component: subject2
  }
]

const router = new VueRouter({
  //mode: "history",
  base: process.env.BASE_URL,
  mode: 'hash',
  routes
})

export default router
